s {\n  border-bottom-color: rgb(221, 221, 221);\n}\n\n.sidebar-toggle {\n  padding: 6px 8px;\n}\n\n/* Style the modal */\n\n.modal-sm {\n  min-width: 60vw;\n  max-height: 90vh;\n}\n\n/* Get rid of ugly number input spinner in chrome and ff*/\ninput[type=number]::-webkit-inner-spin-button,\ninput[type=number]::-webkit-outer-spin-button {\n    -webkit-appearance: none;\n    margin: 0;\n}\ninput[type=number] {\n    -moz-appearance:textfield;\n}\n\n/* Fade hiding animation in ng-repeat*/\n.repeat-animate.ng-enter\n{\n -webkit-transition: opacity cubic-bezier(0.250, 0.250, 0.750, 0.750) 0.1s;\n  transition: opacity cubic-bezier(0.250, 0.250, 0.750, 0.750) 0.1s;\n  opacity: 0;\n}\n\n.repeat-animate.ng-enter-active {\n    opacity: 1;\n}\n\n\n/* Container which centers content vertically and horizontally*/\n.centre-container {\n  display: flex;\n  height: 100%;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n  flex-wrap: nowrap;\n}\n\n.centre-content {\n  text-align: center;\n  width: 40%;\n  padding-bottom: 100px;\n}\n\n.main-container {\n  height: 100%;\n  padding-top: 50px;\n  box-sizing: border-box;\n}\n\n.stupid-adsbygoogle {\n  position: absolute;\n  bottom: 40px;\n  height: 30vh;\n  width: 60vw;\n  left: 0;\n  right: 0;\n  margin-left: auto;\n  margin-right: auto;\n}\n\n/* Can be applied to any class to remove all padding*/\n.nopadding {\n   padding: 0 !important;\n   margin: 0 !important;\n}\n.no-left-right-padding {\n  padding-right: 0 !important;\n  padding-left: 0 !important;\n}\n.no-up-down-padding {\n  padding-top: 0 !important;\n  padding-bottom: 0 !important;\n  margin-top: 0 !important;\n  margin-bottom: 0 !important;\n}\n\n/* Navbar brand stuff */\n.navbar-default {\n  font-size: 18px;\n  margin-bottom: 0px;\n  z-index: 10;\n  position: absolute;\n  width: 100%;\n}\n.navbar-default a:visited{\n  color: white !important;\n}\n.navbar-default a:hover{\n  color: #c1ffdc !important;\n}\n.navbar-default a:active{\n  color: white !important;\n}\n.navbar-default {\n  background-color: #0089CC;\n  color: white;\n}\n\n.navbar-brand {\n    color: white !important;\n}\n.navbar-brand-centred {\n    position: absolute;\n    left: 50%;\n    display: block;\n    padding-left: 0px;\n    padding-right: 0px;\n    width: 210px;\n    text-align: center;\n    font-size: 36px;\n}\n.navbar>.container-fluid .navbar-brand-centred {\n  margin-left: -105px !important;\n}\n.navbar-default .navbar-toggle span {\n  color: #0089CC;\n}\n\n.navbar-toggle {\n  padding: 3px 6px;\n  margin-right: 10px;\n  background-color: white;\n}\n\n.navbar-collapse, .navbar-form {\n  border-style: none;\n  box-shadow: none;\n}\n.navbar>.container .navbar-brand-centred,\n.navbar>.container-fluid .navbar-brand-centred {\n    margin-left: -125px;\n}\n\n/* My cool button */\n.btn-cool {\n  background-color: #28A15D;\n  border-color: #036D31;\n  color: white;\n}\n.btn-cool:visited {\n  background-color: #28A15D;\n  color: white;\n}\n.btn-cool:hover {\n  background-color: #118945;\n  color: white;\n}\n.btn-cool:active {\n  color: white;\n}\n.btn-cool:focus {\n  background-color: #118945;\n  color: white;\n}\n\n.map-icon-practice {\n  padding-top: 8px;\n}\n\n.map-icon-text {\n  font-size: 12px;\n  font-weight: bold;\n  color: white;\n}\n\n#map_view {\n  overflow-y: hidden;\n}\n\n/* Expand down the list and canvas */\n.animate-expand-down {\n  margin: 0;\n  padding: 0;\n  -webkit-transform-style: preserve-3d;\n  -webkit-transition: max-height 0.5s;\n  -moz-transition: max-height 0.5s;\n  -ms-transition: max-height 0.5s;\n  -o-transition: max-height 0.5s;\n  transition: max-height 0.5s;\n}\n#practice-list {\n  overflow-y: auto;\n  overflow-x: hidden;\n}\n#map_canvas {\n  overflow: hidden;\n}\n\n.active {\n  -webkit-transform-style: preserve-3d;\n  transition: background .4s linear;\n  -webkit-transition-duration: 0.4s;\n  -moz-transition-duration: 0.4s;\n}\n\n.hidden {\n  display: none;\n}\n\n/* Custom page footer */\n.footer {\n  position: relative;\n  height: 50px;\n  margin-top: -50px;\n  text-align: center;\n  clear: both;\n}\n.fb-like{\n  position: absolute !important;\n  z-index: 1;\n  top: 15px;\n  left: 65px;\n}\n\n/* Responsive: Portrait tablets and up */\n@media screen and (min-width: 768px) {\n  .container {\n    max-width: 730px;\n  }\n\n  .appLink {\n    right: 0px;\n    top: 5px;\n  }\n}\n\n/* Navbar search stuff */\n.navbar-form.ng-hide-add.ng-hide-add-active,\n.navbar-form.ng-hide-remove.ng-hide-remove-active {\n  -webkit-transition: 600ms cubic-bezier(0.420, 0.000, 0.580, 1.000) all;\n  -moz-transition: 600ms cubic-bezier(0.420, 0.000, 0.580, 1.000) all;\n  -ms-transition: 600ms cubic-bezier(0.420, 0.000, 0.580, 1.000) all;\n  -o-transition: 600ms cubic-bezier(0.420, 0.000, 0.580, 1.000) all;\n  transition: 600ms cubic-bezier(0.420, 0.000, 0.580, 1.000) all;\n}\n.navbar-nav {\n  padding: 0;\n  margin: 0;\n}\n#info-button {\n  width: 30px;\n}\n\n#info-button a {\n  padding: 15px 10px;\n}\n\n.navbar-form.ng-hide {\n  opacity: 0;\n  line-height: 0;\n  padding: 0 10px;\n}\n\n/* View transition stuff */\n.view-animate {\n  height: 100%;\n}\n.view-animate.ng-enter, .view-animate.ng-leave {\n  transition: transform 500ms ease;\n  -moz-transition: transform 500ms ease;\n  -webkit-transition: transform 500ms ease;\n  -webkit-transform-style: preserve-3d;\n  -webkit-backface-visibility: hidden;\n  -webkit-perspective: 1000;\n  display:block;\n  width:100%;\n  position:absolute;\n  top:0;\n  left:0;\n  right:0;\n  bottom:0;\n  padding: 0;\n  margin: 0;\n  padding-left:15px;\n  padding-top:52px;\n}\n.view-animate.ng-enter{\n  transform: translate3d(100%, 0, 0);\n  -moz-transform: translate3d(100%, 0, 0);\n  -webkit-transform: translate3d(100%, 0, 0);\n}\n.view-animate.ng-leave{\n  transform: translate3d(0, 0, 0);\n  -moz-transform: translate3d(0, 0, 0);\n  -webkit-transform: translate3d(0, 0, 0);\n}\n.view-animate.ng-leave.ng-leave-active {\n  transform: translate3d(-100%, 0, 0);\n  -moz-transform: translate3d(-100%, 0, 0);\n  -webkit-transform: translate3d(-100%, 0, 0);\n}\n.view-animate.ng-enter.ng-enter-active {\n  transform: translate3d(0, 0, 0);\n  -moz-transform: translate3d(0, 0, 0);\n  -webkit-transform: translate3d(0, 0, 0);\n}\n\n/* Expand to full height */\n.animate-expand {\n  line-height: 25px;\n  overflow: hidden;\n  -webkit-transition: all 0.5s;\n  -moz-transition: all 0.5s;\n  transition: all 0.5s;\n}\n.animate-expand.ng-hide-add,\n.animate-expand.ng-hide-remove {\n  display: block !important;\n  line-height: 0px;\n}\n.animate-expand.ng-hide-remove.ng-hide-remove-active {\n  line-height: 25px;\n}\n.animate-expand.ng-hide-add {\n  line-height: 25px;\n}\n\n.sidebar-offcanvas {\n  transform: none;\n}\n\n#overlay {\n  display: none;\n}\n\n/*\n * Off Canvas\n * --------------------------------------------------\n */\n@media screen and (max-width: 767px) {\n  .appLink {\n    right: -15px;\n    bottom: 15px;\n  }\n\n  .appLink img {\n    height: 100%;\n    width: 80%;\n  }\n\n  .fb-like{\n    left: 2px !important;\n    top: auto;\n    bottom: 5px;\n  }\n  .sidebar-offcanvas {\n    position: absolute;\n    -webkit-transform-style: preserve-3d;\n    -webkit-transition: -webkit-transform .20s ease-out;\n    -moz-transition: -moz-transform .20s ease-out;\n    -ms-transition: -ms-transform .20s ease-out;\n    -o-transition: -o-transform .20s ease-out;\n    transition: transform .20s ease-out;\n    box-shadow: 2px 0px 10px #888888;\n    margin-top: 1px;\n    z-index: 8;\n    width: 90vw;\n    background-color: white;\n  }\n\n  #overlay {\n    -webkit-transform-style: preserve-3d;\n    -webkit-transition: opacity .20s ease-out;\n    -moz-transition: opacity .20s ease-out;\n    -ms-transition: opacity.20s ease-out;\n    -o-transition: opacity .20s ease-out;\n    transition: opacity .20s ease-out;\n    background-color: black;\n    position: absolute;\n    opacity: 0.4;\n    height: 100vh;\n    width: 100vw;\n    z-index: 7;\n    display: initial;\n    overflow-y: hidden;\n  }\n\n}\n\n.fa-window-close {\n  cursor: pointer;\n}\n\n/* Font size and autocomplete size on really small screens*/\n@media screen and (max-width: 420px) {\n\n  .centre-content, .stupid-adsbygoogle {\n    width: 80vw;\n  }\n\n  .stupid-adsbygoogle {\n    height: 120px;\n  }\n\n  .modal-body {\n    max-height: 80vh;\n    overflow: auto;\n  }\n  .pac-container {\n    width: 100% !important;\n    left: 0px !important;\n  }\n  .tagline {\n    padding-bottom: 30px;\n  }\n  .navbar-brand-centred {\n    font-size: 28px;\n    width: 170px;\n  }\n  .navbar>.container-fluid .navbar-brand-centred {\n    margin-left: -80px !important;\n  }\n\n  ::-webkit-input-placeholder { font-size: 85%; }\n  ::-moz-placeholder { font-size: 85%; }\n  :-ms-input-placeholder { font-size: 85%; }\n  input:-moz-placeholder { font-size: 85%; }\n\n  h4 {\n    font-size: 16px;\n  }\n}\n\n\n/* Google Autocomplete stuff */\n.big-autocomplete .pac-item {\n  height: 50px !important;\n  font-size: 16px !important;\n}\n.big-autocomplete .pac-item-query {\n  font-size: 18px !important;\n}\n\n.small-autocomplete .pac-container {\n  width: 24em !important;\n}\n"],"sourceRoot":""}]);
